<template>
  <div>
    <div class="header animated fadeInDown">
      <p class="header-nav">
        <router-link to="/photo" :class="activeClass == 0 ? 'active':''">全部</router-link>
        <router-link to="/photo?sm_class=1" :class="activeClass == 1 ? 'active':''">古韵</router-link>
        <router-link to="/photo?sm_class=2" :class="activeClass == 2 ? 'active':''">山水</router-link>
        <router-link to="/photo?sm_class=3" :class="activeClass == 3 ? 'active':''">独家</router-link>
      </p>
    </div>
    <div class="p-container">
       <photo_falls :deta="!transfer_article  ? article : transfer_article" v-if="isRouterAlive"></photo_falls>
    </div>
  </div>
</template>

<script>
// 父组通过  :deta="data中的数据"  的方式将数据传递给子组件
import photo_falls from "@/components/basis/photo_falls"
export default {
  components: {
     photo_falls
  },
  data() {
    return {
      isRouterAlive: true,
      activeClass:0,
      name : '相册',
      article:[
        {id:1,sm_class:'1',title:'彩虹之国-南非之旅',img_url:'/static/img/article/1.jpg',describe:'梦之天堂，遥远的地方'},
        {id:2,sm_class:'2',title:'彩虹之国-南非之旅',img_url:'/static/img/article/2.jpg',describe:'梦之天堂，遥远的地方'},
        {id:3,sm_class:'3',title:'彩虹之国-南非之旅',img_url:'/static/img/article/3.jpg',describe:'梦之天堂，遥远的地方'},
        {id:4,sm_class:'1',title:'彩虹之国-南非之旅',img_url:'/static/img/article/4.jpg',describe:'梦之天堂，遥远的地方'},
        {id:5,sm_class:'2',title:'彩虹之国-南非之旅',img_url:'/static/img/article/5.jpg',describe:'梦之天堂，遥远的地方'},
        {id:6,sm_class:'3',title:'彩虹之国-南非之旅',img_url:'/static/img/article/6.jpg',describe:'梦之天堂，遥远的地方'},
        {id:7,sm_class:'1',title:'彩虹之国-南非之旅',img_url:'/static/img/article/7.jpg',describe:'梦之天堂，遥远的地方'},
        {id:8,sm_class:'2',title:'彩虹之国-南非之旅',img_url:'/static/img/article/8.jpg',describe:'梦之天堂，遥远的地方'},
        {id:9,sm_class:'3',title:'彩虹之国-南非之旅',img_url:'/static/img/article/9.jpg',describe:'梦之天堂，遥远的地方'},
        {id:10,sm_class:'3',title:'彩虹之国-南非之旅',img_url:'/static/img/article/10.jpg',describe:'梦之天堂，遥远的地方'},
      ],
      transfer_article : this.article,
      clss:'',
    };
  },
  props: {
    //接收父组件传递过来的参数方法
    deta: {
      default: function() {
        return '';
      }
    }
  },
  beforeCreate(){
    //实例未创建，调用不到data数据
  },
  created() {
    //实例创建完成后调用,此阶段完成了数据的观测,可调用data数据
    if(this.$route.query.sm_class == undefined){
      this.activeClass = 0
    }else{
      this.activeClass = this.$route.query.sm_class
    }
    this.navActive(this.activeClass)
  },
  methods: {
    //定义方法的地方
    //监听方法click事件等，执行drawFeatures方法
    reload () {
      this.isRouterAlive = false
      this.$nextTick(() => (this.isRouterAlive = true))
    },
    navActive(a){
      // this.activeClass = a;
      if(a == 0){
        this.transfer_article = [].concat(this.article)
      }else{
        let arr = [];
        for(let i = 0;i < this.article.length;i++){
          if(this.article[i].sm_class == a){
            arr.push(this.article[i])
          }
        }
        this.transfer_article = [].concat(arr)
      }
    }
  },
  
  mounted() {
    //页面初始化方法
    this.navActive(0)
  },
  watch: {
      // transfer_article:function(){
      //   this.reload ()
      // },
      $route(){
        //this.clss = this.$route.query.sm_class
        if(this.$route.query.sm_class == undefined){
          this.activeClass = 0
        }else{
          this.activeClass = this.$route.query.sm_class
        }
      },
      activeClass() {
        this.reload()
        this.navActive(this.activeClass)
      }
  }
};
</script>

<style scoped>
.header{
  width: 100%;
  height:400px;
  position: relative;
  background: #fff url(/static/img/photo/banner.jpg) no-repeat center;
  background-size: cover;
}
.header img{
  display: block;
}
.header p{
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  background: rgba(56, 134, 72, 0.43);
}
.header-nav a{
  display: inline-block;
  width: 80px;
  font-size: 1rem;
  color: #fafafa;
  line-height: 40px;
  text-align: center;
  cursor: pointer;
  transition: all .5s;
}
.header-nav .active,
.header-nav a:hover{
  border-radius: 50px 0 50px 0;
  background: #58950a;
}
.p-container{
  width: 100%;
}
@media (max-width: 1200px) {
  .header{
    height:300px;
  }
}
@media (max-width: 750px) {
  .header{
    height:150px;
  }
  .header-nav span{
    width: 60px;
    font-size: .9rem;
    line-height: 30px;
  }
}
</style>



